import React, { Component } from "react";
import HeaderNav from "../components/HeaderNav";
import HeaderNavMobile from "../components/HeaderNavMobile";
import ContactForm from "../components/ContactForm";
import HomeDivider from "./HomeDivider";
import HomeContent from "./HomeContent";
import HeadshotAndLinks from "./HeadshotAndLinks";

class HomeDesktop extends Component {
  constructor(props) {
    super(props);
    this.home_content = [React.createRef(), React.createRef()];
    this.state = {
      vpwidth: 0,
      vpheight: 0,
      vpratio: 0,
      vpscroll: 0,
      headerVisible: true,
      contentVis: [false, false],
      animate: [false, false]
    };
  }

  handleScroll = event => {
    this.setState({ vpscroll: window.pageYOffset });
    for (let ref in this.home_content) {
      if (this.isInViewport(this.home_content[ref])) {
        if (!this.state.contentVis[ref]) {
          let contentVisCopy = this.state.contentVis.slice(0);
          let animateCopy = this.state.animate.slice(0);
          animateCopy[ref]=true;
          contentVisCopy[ref] = true;
          
          this.setState({
            contentVis: contentVisCopy,
            animate:animateCopy
          });
        }
      } else {
        if (this.state.contentVis[ref]) {
          let contentVisCopy = this.state.contentVis.slice(0);
          contentVisCopy[ref] = false;
          let animateCopy = this.state.animate.slice(0);
          animateCopy[ref]=false;
          this.setState({
            contentVis: contentVisCopy,
            animate:animateCopy
          });
        }
      }
    }
    console.log(this.state.contentVis);
    if (this.state.vpscroll > this.state.vpheight / 1.5) {
      this.setState({ headerVisible: false });
    } else {
      this.setState({ headerVisible: true });
    }
  };
  componentDidMount() {
    this.setState({
      vpwidth: window.innerWidth,
      vpheight: window.innerHeight,
      vpscroll: window.pageYOffset,
      vpratio: window.devicePixelRatio
    });
    window.addEventListener("scroll", this.handleScroll.bind(this));
    console.log(this.home_content);
  }
  isInViewport = ref => {
    const visPinchIn = 220;
    const getTrueOffset = elem => {
      return elem.offsetParent
        ? elem.offsetTop + getTrueOffset(elem.offsetParent)
        : elem.offsetTop;
    };
    if (ref.current) {
      if (ref.current.wrapperRef) {
        return this.isInViewport(ref.current.wrapperRef);
      } else {
        const bounding = ref.current;
        const trueOffset = getTrueOffset(bounding);
        let results =
          trueOffset < this.state.vpscroll + this.state.vpheight - visPinchIn&&
          trueOffset > this.state.vpscroll - bounding.clientHeight +visPinchIn;

        return results;
      }
    } else {
      return null;
    }
  };
  resetAnimation(i, e) {
    let animateCopy = this.state.animate.slice(0);
    animateCopy[i] = false;
    this.setState({
      animate: animateCopy
    });
  };
  render() {
    console.log(`'url(${window.location.pathname.split('/')[1]}/images/aboutBack.jpg')`);
    const icons = ["ndrecruit", "encontro", "quote", "fborm"];
    return (
      <div>
        {!this.props.mobile ? (
          <HeaderNav selected={"Home"} visible={this.state.headerVisible} />
        ) : (
          <HeaderNavMobile selected={"Home"} />
        )}
        <div className="page flex-column">
          <section
            className={`home-page flex-row`}
            style={{ width: "100%", padding: "0",backgroundImage:`url('/${window.location.pathname.split('/')[1]}/images/aboutBack.jpg')` }}
          >
            <div className="flex-column welcome-wrap">
              <div className="page-welcome">Welcome</div>
              <p className="home-introduction">
                Hello, I'm Patrick Gerspach and welcome to my website! I'm a
                full stack developer with a love for programming. I'm at my
                happiest when I'm solving mind-bending coding problems, hacking
                away on a project, or helping others set up their personal
                websites for the world to see!
              </p>
            </div>
            <div className="flex-column desktop-headshot">
              <HeadshotAndLinks />
            </div>
          </section>
          <HomeContent
            icons={icons}
            mobile={false}
            text="With experience in multiple languages and frameworks spanning
                from front end to back end, I am capable of building complete web applications."
            row={true}
            reverse={true}
            color="linear-gradient(var(--color5), white)"
            ref={this.home_content[0]}
            animate = {this.state.animate[0]}
          />

          {/* <HomeDivider
            mobile={false}
            text="Coding with integrity and the resolve to improve."
          /> */}

          <HomeContent
            icons={"running"}
            mobile={false}
            text="From the moment I began web development, I made it my mission to
                get better every day. I am continuously learning new technologies, and putting them to use in projects."
            row={false}
            reverse={false}
            color="linear-gradient(-90deg, var(--color3),var(--color1))"
            ref={this.home_content[1]}
            animate = {this.state.animate[1]}
          />

          {/* <HomeDivider
            mobile={false}
            text="Embracing challenges as a welcome friend."
          /> */}
          {/* <HomeContent
            icons={"gears"}
            mobile={false}
            text="With a background in engineering, I consider wrestling problems
                to be a comfort zone at this point."
            row={false}
            reverse={true}
            color="linear-gradient(var(--color5), var(--color5))"

          /> */}

          <div
            style={{
              backgroundColor: "var(--color1)",
              width: "auto",
              height: "auto"
            }}
            className="contact-wrap flex-column"
          >
            <div style={{ alignSelf: "flex-start", fontSize: "35px" }}>
              Contact
            </div>
            <p style={{ fontSize: "22px" }}>
              I would love to talk. Please fill out your information with a
              message and I will reach out to you. Or, you can contact me
              directly at the email and phone provided above.
            </p>
            <div
              style={{
                width: "90%",
                alignSelf: "center",
                alignItems: "center",
                justifyContent: "center"
              }}
              className={`flex-row`}
            >
              <ContactForm color="var(--color1)" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default HomeDesktop;
