import React, { Component } from "react";
import IconRow from "../components/IconRow";
import CircleImage from "../components/CircleImage";

class HomeContent extends Component {
  constructor(props){
    super(props);
    this.wrapperRef = React.createRef();

  }
  render() {
    const slideInDistance = "60px"
    return (
      <div
        style={{ background: `${this.props.color}` }}
        className={`about-passion-wrap ${
          this.props.mobile ? "flex-column" : "flex-row"
        }`}
        ref = {this.wrapperRef}
      >
        {!this.props.reverse ? (
          <div
            className={`about-passion-side ${
              this.props.mobile ? "flex-row" : "flex-column"
            }`}
            style={{
              color: "var(--color2)",
              padding: `${this.props.mobile ? "20px" : "50px"}`
            }}
          >
            <p  style={{position:"relative", right:slideInDistance, animation:this.props.animate?"slideInRight 1s ease forwards":null}}>{this.props.text}</p>
          </div>
        ) : null}
        <div
          className={`about-passion-side left ${
            this.props.mobile ? "flex-row" : "flex-column"
          }`}
        >
          <div className={`about-passion-icon-wrap`}>
            {this.props.row ? (
              <IconRow
                jpg={false}
                numRows={this.props.icons.length > 2 ? 2 : 1}
                size={`${this.props.mobile ? "130" : "160"}`}
                radius={"5"}
                circle={false}
                icons={this.props.icons}
                shadow={5}
              />
            ) : (
              <CircleImage
                imageId={`${this.props.icons}`}
                imageSource={`./images/${this.props.icons}.png`}
                size={`100%`}
                circle={false}
                radius={0}
                shadow={false}
              />
            )}
          </div>
        </div>
        {this.props.reverse ? (
          <div
            className={`about-passion-side ${
              this.props.mobile ? "flex-row" : "flex-column"
            }`}
            style={{
              color: "var(--color2)",
              padding: `${this.props.mobile ? "20px" : "50px"}`
            }}
          >
            <p style={{position:"relative", left:slideInDistance, animation:this.props.animate?"slideInLeft 1s ease forwards":null}}>{this.props.text}</p>
          </div>
        ) : null}
      </div>
    );
  }
}
export default HomeContent;
